<template>
  <div>
    <a-table rowKey="id" :columns="columns" :data-source="rightsList" bordered :pagination="false">
      <template #index="{index}">
          {{index}}
      </template>
      <template #level="{text}">
        <a-tag color="green" v-if="text==0">一级</a-tag>
        <a-tag color="blue" v-if="text==1">二级</a-tag>
        <a-tag color="pink" v-if="text==2">三级</a-tag>
      </template>
    </a-table>

  </div>
</template>

<script>
import { message } from 'ant-design-vue'
import { getCurrentInstance, onMounted, ref } from 'vue'
export default {
  setup () {
    // 获取代理的实例
    const { proxy } = getCurrentInstance()
    // 表格的数据源
    const rightsList = ref()
    // 获取权限列表的函数
    const queryRightsList = async () => {
      const { meta, data } = await proxy.$http.get('rights/list')
      if (meta.status != 200) return message.error(meta.msg)
      rightsList.value = data
    }
    onMounted(() => {
      queryRightsList()
    })

    const columns = [
      {
        title: '#',
        slots: { customRender: 'index' }

      },
      {
        title: '权限名称',
        className: 'column-money',
        dataIndex: 'authName'
      },
      {
        title: '路径',
        dataIndex: 'path'
      },
      {
        title: '权限等级',
        dataIndex: 'level',
        slots: { customRender: 'level' }

      }

    ]
    return {
      columns,
      rightsList
    }
  }
}
</script>

<style scoped>

</style>
